header{
    height:100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;

}

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    font-size: 1.4rem;
    /* background: black; */
}

.header__socials::after{
    content:"" ;
    width: 4px;
    height: 2rem;
    background:brown;
}

.me{
    background: linear-gradient(white, transparent);
    width:15rem;
    height: 15rem;
    position:absolute;
    left:calc(50% - 7.5rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    /* overflow: hidden; */
    padding: 1rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight:500;
    font-size: 0.9rem;
}
@media screen and (max-width:1024px) {
    header{
        height:100vh;
    }
    .me{
        width:12rem;
    /* height: 15rem; */
    position:absolute;
    left:calc(50% - 6rem);
    border-radius: 6rem 6rem 0 0;
    /* overflow: hidden; */
    padding: 0rem 0rem 0rem 0rem;
    }
    
}
@media screen and (max-width:600px) {
    header{
        height:100vh;
    }
    
    .header__socials,
    .scroll__down{
        display:none;
    }
}