@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
*{
  margin: 0;
  padding:0;
  border:0;
  outline:0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Poppins', sans-serif;
  line-height: 1.7;
  background-color: black;
  color: rgb(255, 255, 255);
}
.container{
  width:90%;
  margin: 0 auto;
  /* margin-bottom: 10rem; */
}

h1,
h2,
h3,
h4,
h5{
font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
  
  
}
section > h2,
section > h5{
text-align: center;
color: whitesmoke;
}

section > h2{
  color: antiquewhite;
  margin-bottom: 3rem;
} 

.text-light{
  color: aliceblue;
}

a{

  color: brown;
  transition: all 400ms ease;
}

a:hover{
  color: azure;
}

.btn{
  width: max-content;
  display: inline-block;
  color: aliceblue;
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid bisque;
  transition: all 400ms ease-in-out;
}

.btn:hover{
  background: rgb(96,95,95);
  color: black;
  border-color: transparent;
}

.btn-primary{
  background: bisque;
  color: black;
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}

@media screen and (max-width: 1024px) {
  .container{
    width: 86%;
  }
  section > h2{
    margin-top: 1rem;
  }
}

@media screen and (max-width: 600px) {
  .container{
    width: 70%;
  }
  section > h2{
    margin-top: 2rem;
  }
}