nav{
    background: transparent;
    display: block;
    padding: 0.3rem 1.7rem;
    z-index:2;
    position: fixed;
    border: 1px solid bisque;
    justify-content: center;
    /* left: calc(50% - 10rem); */
    display: flex;
    align-items: center;
    gap:0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 1rem;
    margin-inline: 1rem;
    border-radius: 50%;
    display: flex;
    flex-wrap: wrap;
    
    color: bisque;
    font-size: 1rem;
}

nav a:hover{
    background: grey;
}

nav a.active{
    background: grey;
    color:aliceblue;
}

@media screen and (min-width: 600px)
{
    nav{
        top: 1rem;
        width: auto;
        left: calc(50% - 16rem);
       
    }
    nav a{
        /* padding-inline: 13%; */
        font-size: 1.2rem;
    }
}
@media screen and (max-width: 600px)
{
    nav{
        bottom: 1rem;
        width: auto;
        left: calc(50% - 11rem);
    }
    nav a{
        margin-inline: 0rem;
        
    }
}