.about__container{
    display:grid;
    grid-template-columns: 1fr;
    gap: 15%;
}

.about__me{
    width:100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        white,
        transparent
    );
    display: grid;
    place-items: center;
}
h5{
    margin-bottom: 1rem;
}



.edu{
    text-align: left;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
}
.about__me-image{
    border-radius:2rem;
    overflow:hidden;
    transform:rotate(10deg);
    transition: all 400ms ease-in-out;;
}

.about__me-image:hover{
    transform:rotate(0);
}

.about__cards{
    display:grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 7.5rem;
}

.about__card{
    background:rgb(96,95,95);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    
    text-align: center;
    transition: all 400ms ease-out;
}

.about__card:hover{
    background: transparent;
    border-color: white;
    cursor: default;
}

.about__icon{
    color: white;
    font-size:1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.7rem;
    color: bisque;
    font-weight: 500;
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: grey;
}

@media  screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
    gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr ;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;;
    }
}

